/**
 * @fileoverview A component for rendering a list of movies that we want to watch.
 */
import React, { useState, useEffect } from 'react';
import { List, Avatar, Spin } from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';
import { useContext } from 'react';
import { FirebaseContext } from '../../components/Firebase/firebase';
import AddMovieModal from './AddMovieModal';

export default function ToWatchList(props) {
  const [isLoaded, setIsLoaded] = useState(false);
  const [movies, setMovies] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const firebase = useContext(FirebaseContext);

  useEffect(() => {
    const onSuccess = (movies) => {
      setIsLoaded(true);
      setMovies(movies);
    };

    const onError = (err) => {
      setIsLoaded(true);
      console.error(err);
    };

    firebase.getMoviesToWatch(onSuccess, onError);
  }, []);

  function addMovie(movie) {
    firebase.saveMovie(movie);
    setMovies([...movies, movie]);
  }

  return isLoaded ? (
    <>
      <PlusCircleOutlined 
        role="button"
        tabIndex={0}
        onClick={() => setShowModal(true)}
        style={{ fontSize: '24px' }}
      />
      <List
        dataSource={movies}
        renderItem={(item) => (
          <List.Item key={item.title}>
            <List.Item.Meta
              avatar={
                <Avatar shape="square" size="large" src={item.avatarUrl} />
              }
              title={item.title}
              description={item.description}
            />
          
          </List.Item>
        )}
      />
      <AddMovieModal
        showModal={showModal}
        addMovie={addMovie}
        closeModal={() => setShowModal(false)}
      />
    </>
  ) : (
    <Spin size="large" />
  );
}