/**
 * @fileoverview a component to add a new movie to the database.
 */
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Modal, Input, Spin, List, Avatar } from 'antd';
import useMovieSearch from '../search/useMoviesSearch';

export default function AddMovieModal(props) {
  const { showModal, addMovie, closeModal } = props;
  const [searchQuery, setSearchQuery] = useState('');
  const { movies } = useMovieSearch(searchQuery);

  function onOk() {
    console.log('Ok');
  }

  return (
    <Modal
      title="Add Movie"
      visible={showModal}
      onOk={onOk}
    >
      <Input
        placeholder="Search Movies"
        onChange={(e) => setSearchQuery(e.target.value)}
      />
      <List
        dataSource={movies}
        renderItem={(item) => (
          <List.Item 
            key={`${item.title}-${Math.ceil(Math.random() * 1000)}`}
            onClick={() => {
              addMovie(item);
              closeModal();
            }}
            style={{ cursor: 'pointer' }}
          >
            <List.Item.Meta
              avatar={
                <Avatar shape="square" size="large" src={item.avatarUrl} />
              }
              title={item.title}
              description={item.description}
            />
          </List.Item>
        )}
      />
    </Modal>
  );
}

AddMovieModal.propTypes = {
  showModal: PropTypes.bool.isRequired,
  addMovie: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired
};